.contents {
    /* border: 1px solid #919866; */
    text-align: center;
    display: inline-block;

    width: 90%;
    margin-bottom: 20px;

    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-items: center;
}

.work{
    border: 1px solid #919866;
    text-align: center;
    

    width: 80%;
    height: 200px;
    margin-bottom: 20px;

    
}
.contents > div {
    transition: all 1s;

    border-radius: 15px;
    overflow: hidden;
}
.contents > div:hover {
    box-shadow: 0px 0px 40px rgb(12 12 12 / 40%);
    transform: scale(1.06, 1.06);
}